@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
html {
    box-sizing: border-box;
}

* {
    margin: 0;
    padding: 0;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

input,
textarea,
select,
button {
    outline: none;
}

input {
    line-height: normal;
}

label,
button {
    cursor: pointer;
}

a {
    text-decoration: none;
}

img {
    display: block;
    max-width: 100%;
}

.header-nav {
    padding: 20px;
    display: flex;
    justify-content: space-between;
}

.page-containter {
    display: flex;
    justify-content: center;
    height: 100vh;
}
.cart-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.cart-items {
    text-align: center;
    margin-top: 4rem;
    background-color: #f8f9fa;
    box-shadow: 0px 0px 15px 5px #fec5bb;
    border-radius: 14px;
    font-size: 1.5rem;
    padding: 2rem;
}

.cart-item {
    display: flex;
    align-items: center;
    margin-bottom: 3rem;
}

.cart-header {
    font-size: 2rem;
    font-weight: 800;
}

.cart-item-img {
    border-radius: 50%;
    object-fit: cover;
    width: 70px;
    height: 70px;
    margin: 0rem 2rem 0 0;
}

.cart-total {
    font-weight: 800;
}

.cart-checkout {
    border: none;
    background-color: #fec5bb;
    border-radius: 14px;
    padding: 1rem;
    margin-top: 2rem;
    cursor: pointer;
    font-size: 1.5rem;
    transition: 200ms ease-in-out;
}

.cart-checkout:hover,
.cart-gohome:hover {
    background-color: #ff8fa3;
}

.cart-gohome {
    border: none;
    background-color: white;
    border-radius: 14px;
    padding: 1rem;
    margin-top: 2rem;
    cursor: pointer;
    font-size: 1rem;
    transition: 200ms ease-in-out;
}
.dogs-container {
    display: flex;
    flex-direction: row;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 1rem;
    text-align: center;
}

.dogs {
    background-color: #F7CAC9;
    padding: 1rem;
    border-radius: 14px;
    width: 350px;
    height: 450px;
    margin-bottom: 1.5rem;
    display: grid;
    align-items: center;
    grid-template-areas: "img info" "img info" "desc desc" "price price" "btn    btn";
}

.dogs-info {
    grid-area: info;
}

.dogs-img-container {
    grid-area: img;
    width: 150px;
    height: 150px;
    overflow: hidden;
}

.dogs-desc {
    grid-area: desc;
}

.dogs-price {
    font-weight: 700;
    margin: 1rem 0 1rem 0;
    grid-area: price;
}

.dogs-btn {
    padding: 0.35rem;
    border-radius: 14px;
    cursor: pointer;
    background-color: #e8e8e4;
    grid-area: btn;
    border: none;
    font-size: 1.25rem;
    transition: 100ms ease-in-out;
}

.dogs-btn-disabled {
    padding: 0.35rem;
    border-radius: 14px;
    background-color: #818a87;
    grid-area: btn;
    border: none;
    font-size: 1.25rem;
    transition: 100ms ease-in-out;
}

.dogs-btn:hover {
    background-color: #0a9396;
    color: white;
}

.dog-img {
    width: 100%;
    width: 120px;
    height: 120px;
    object-fit: cover;
    border-radius: 50%;
}
:root {
    --pink-color: #f2726a;
}

section {
    font-size: 25px;
    text-align: center;
    display: flex;
    flex-direction: column;
}
.navbar {
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
    padding: 20px 20px 0;
}

.navbar>* {
    margin-left: 2rem;
    text-decoration: none;
    transition: 200ms ease-in-out;
}

.navbar>*:hover {
    color: #7510e8;
}
* {
    box-sizing: border-box;
}

body {
    background: #e6eff0 !important;
    min-height: 100vh;
    display: flex;
    font-weight: 400;
    font-family: 'Fira Sans', sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
label,
span {
    font-weight: 500;
    font-family: 'Fira Sans', sans-serif;
}

body,
html,
.App,
#root,
.auth-wrapper {
    width: 100%;
    height: 100%;
}

.navbar-light {
    background-color: #ffffff;
    box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}

.auth-wrapper {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: left;
}

.auth-inner {
    width: 450px;
    margin: auto;
    background: #ffffff;
    box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
    padding: 40px 55px 45px 55px;
    border-radius: 15px;
    transition: all .3s;
}

.auth-wrapper .form-control:focus {
    border-color: #167bff;
    box-shadow: none;
}

.auth-wrapper h3 {
    text-align: center;
    margin: 0;
    line-height: 1;
    padding-bottom: 20px;
}

.custom-control-label {
    font-weight: 400;
}

.forgot-password,
.forgot-password a {
    text-align: right;
    font-size: 13px;
    padding-top: 10px;
    color: #7f7d7d;
    margin: 0;
}

.forgot-password a {
    color: #167bff;
}
